.firstPage {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
}

.vinDecoder {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.vinHeader,
.vinFinder {
  width: 100%;
  @include flexCenter(center, row, stretch);
}
.vinFinder {
  min-height: 20vh;
  align-items: center;
}
#InputVin {
  height: 25px;
}
.btn,
.detailsBtn {
  height: 31px;
  background: white;
  border: 1px solid black;
  border-radius: 2px;
}
.vinHeader {
  font-size: 38px;
  min-height: 15vh;
  align-items: flex-end;
}
.vinArticle {
  @include flexCenter(flex-start, column, stretch);
  min-height: 55vh;
  padding: 50px;
  p {
    width: 100%;
    line-height: 1.3;
    padding: 30px 0 30px 0;
  }
  h3 {
    font-size: 25px;
  }
}

main,
aside {
  width: 100%;
  min-height: 100vh;
}

main {
  background: white;
}
.headerRaport {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 20vh;
  background: yellow;
  h3 {
    font-size: 38px;
  }
}

.infoRaport,
.imgRaport {
  @include flexCenter(center, row, center);
  width: 50%;
  font-size: 16px;
}
.mainRaport {
  display: flex;
  width: 100%;
  min-height: 60vh;
}
.detailsRaport {
  @include flexCenter(center, row, center);
  min-height: 20vh;
  background: rebeccapurple;
}
.imgRaport {
  background: white;
  img {
    object-fit: cover;
    width: 90%;
  }
}

.ContainerDetailsRaport {
  min-height: 40vh;
  width: 100%;
  background: orange;
  display: none;
}
